<template>
  <layout :active="4">
    <div class="membercontent">
      <div class="member">
        <ul>
          <li v-for="record in users">
            <router-link class="hover" :to="record.path">
              <i class="fa fa-expand"></i>
              <img :src="record.avatar">
              <span>{{ record.name }}</span><em>{{ record.jobdes }}</em>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </layout>
</template>

<script>
  import layout from '../modules/layout.vue';
  import {userdata} from '../mock-data/userdata.js';
  export default {
    components:{
      layout,
    },
    data(){
      return {
        users:userdata
      }
    },
    created(){
      // console.log(userdata)
    }
  }
</script>

<style lang="less">
  .membercontent{
    position:relative;
    height:100%;
  }
  .member{
    position:absolute;
    margin-top:100px;
    ul li{
      background:#282828;
      width:175px;
      display:inline-block;
      margin:30px 10px 0 10px;
      a{display:block;}
      span,em{
        display:block;
        padding:10px 0 10px 10px;
        color:#b5b5b5;
      }
      span{
        font-size:14px;
        font-weight: bold;
      }
      em{padding-top:0;}
      a:hover{
        text-decoration:none;
        
      }
      a.hover:hover{
        .fa,img,span,em{
          transition: opacity 1s ease-in-out; 
        }
        img,span,em{
          opacity: 0.2;
        }
        .fa{
          opacity: 1;
        }
      }
      .fa{
        color:#fff;
        position: absolute;
        margin-left: 80px;
        margin-top: 77px;
        font-size: 27px;
        opacity: 0;
      }
    }
  }

</style>


